.header {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 10;
    

    // .header__container

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 60px;
    }
    // .header__logo

    &__logo {
        color: $color-primary;
    }

    // .header__main-navigation

    &__main-navigation {
        a {
            text-decoration: none;
            color: #CDCDCD;
            
        }
        
        & .main-navigation__item {
            &:hover a {
                color: $color-light-s;
            }
        }
    }
}

.logo {

    // .logo__head

    &__head {
        @include font('Georgia', normal, 400, 32px, 110%, 0.06em);
        margin-bottom: 10px;
    }

    // .logo__description

    &__description {
        @include font('Arial', normal, normal, 14px, 15px, 0.06em);
        padding-left: 4px;
    }
}

.main-navigation {

    // .main-navigation__list

    &__list {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    // .main-navigation__item

    &__item {
        @include font('Arial', normal, 400, 15px, 160%);
        position: relative;
        display: inline-flex;

        padding: 5px 0;
        margin-right: 31px;

        &:last-child {
            margin-right: 0;
        }

        &--active::after {
            content: '';
            display: inline-block;
            position: absolute;
            left: 0;
            bottom: 5px;
            width: 100%;
            height: 3px;
            background-color: $color-primary;
        }

    }

    
}