.button-primary {
    border: none;
    border-radius: 100px;
    background: $color-primary;

    color: $color-dark-3xl;
    padding: 15px 45px;

    font-family: 'Georgia';
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 130%;
    letter-spacing: 0.06em;
    cursor: pointer;

    &:hover{
        background-color: $color-primary-light;
    }
}